.balls {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  list-style: none;
  padding: 0;
  margin: 0;

  .balls__item:nth-child(1) {
    background-image: url('../img/001.png');
    width: 137px;
    height: 258px;
    display: block;
    top: 400px;
    left: -199px;
  }

  .balls__item:nth-child(2) {
    background-image: url('../img/002.png');
    width: 80px;
    height: 154px;
    display: block;
    top: 21px;
    left: -56px;
  }

  .balls__item:nth-child(3) {
    background-image: url('../img/003.png');
    width: 131px;
    height: 249px;
    display: block;
    top: -19px;
    left: calc(50% - 86px);
  }

  .balls__item:nth-child(4) {
    background-image: url('../img/004.png');
    width: 239px;
    height: 459px;
    display: block;
    top: 300px;
    right: -158px;
  }

  .balls__item:nth-child(5) {
    background-image: url('../img/005.png');
    width: 118px;
    height: 223px;
    display: block;
    top: 226px;
    right: -177px;
  }
}

.balls__item {
  position: absolute;
  visibility: hidden;
  opacity: 0;
  transform: translateY(-50px);
  transition: .5s all linear;

  &.active {
    transform: translateY(0px);
    visibility: visible;
    opacity: 1;
    animation: jello-horizontal 1.5s both;
  }

  &.up {
    transition: 2s all linear;
    top: -500px !important;
  }
}

@keyframes jello-horizontal {
  0% {
    transform: scale3d(1, 1, 1);
  }
  30% {
    transform: scale3d(1.25, 0.75, 1);
  }
  40% {
    transform: scale3d(0.75, 1.25, 1);
  }
  50% {
    transform: scale3d(1.15, 0.85, 1);
  }
  65% {
    transform: scale3d(0.95, 1.05, 1);
  }
  75% {
    transform: scale3d(1.05, 0.95, 1);
  }
  100% {
    transform: scale3d(1, 1, 1);
  }
}
