.main {
  flex-grow: 1;
  background: linear-gradient(to left, $color5 0%, $color5 50%, transparent 50%, transparent 100%), url('../../img/circle-blues.png');
}

.main__container {
  display: flex;
  min-height: 100%;
  position: relative;
}

.main__chat {
  background-color: $color1;
  background-image: url('../../img/circle-blues.png');
  flex: 1 0 50%;
}

.main__board {
  background-color: $color5;
  flex: 1 0 50%;
}
